<template>
    <div>

        <span>{{ movie.movieTitle }}</span>

        <ul>
            <li>{{ movie.rating.rating }}</li>
            <li>{{ movie.movieLength }}</li>
            <li>{{ movie.genre.genre }}</li>
            <li>{{ movie.releaseDate }}</li>
            <li></li>
            <li>{{ movie.director.firstName }} {{ movie.director.lastName }}</li>
            <li></li>
            <li>
                <h2>Actors:</h2>
                <tr v-for="actor in movie.actors" :key="actor.id">
                    <td>{{ actor.firstName }}</td>
                    <td>{{ actor.lastName }}</td>
                    <td>{{ actor.dateOfBirth }}</td>
                </tr>
            </li>
            <li></li>
            <li><sub>{{ movie.overview }}</sub></li>
        </ul>

        <iframe width="560" height="315" :src="movie.trailerUrl" frameborder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen></iframe>

    </div>
</template>

<script>
export default {
    data: () => ({
        movie: null
    }),
    async mounted() {
        const { data } = await this.$http.get('http://3.149.27.3:8080/api/movies/' + this.$route.params.id);
        this.movie = data;
    }
}
</script>